// import UniStatusBar from"../../components/uni-ui/uni-status-bar/uni-status-bar.vue"
export default {
	components:{
		// UniStatusBar
	},
	data() {
		return {
			status:true,
			username:'',
			password:'',
			telephone:'15600000000',
			code:'',
			codeTime:0
		}
	},
	methods: {
		submit(){
			// 表单验证、
			if(!this.validate()) return
		},
		validate(){
			var mPattern =/^1[34578]\d{9}$/
			if(!mPattern.test(this.telephone)){
				uni.showToast({
					title:'手机号格式错误'
				})
				return false
			}
		},
		getCode(){
			if(this.codeTime>0){
				return
			}
			this.codeTime=30
			let timer = setInterval(()=>{
				if(this.codeTime>=1){
				this.codeTime--
				}else{
					this.codeTime=0
					clearInterval(timer)
				}
			},1000)
		},
		initForm(){
			this.username='',
			this.password='',
			this.telephone='',
			this.code=''
		},
		changeLogin(){
			this.initForm()
			this.status=!this.status
		}
	},
	computed:{
		disabled(){
			if((this.username===''||this.password==='')&&(this.telephone===''||this.code==='')){
				return true
			}
			return false
		}
	}
}
